import grpc_wrapper from 'grpc-simple-wrapper';
// const chatbot_kr_filter = grpc_wrapper.client.createClient('155.230.27.179', 50069);
const chatbot_kr_filter = grpc_wrapper.client.createClient('155.230.24.108', 50069);
export const filterKrFinlter = (request) => {
    return new Promise((resolve, reject) => {
        chatbot_kr_filter.send(request, (err, res) => {
            if (err) {
                console.log('err:', err);
                reject(err);
            } else {
                console.log('result:', res);
                resolve(res.output);
            }
        });
    });
};